@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset "UTF-8";
* {
  padding: 0px;
  margin: 0px;
  border: 0px;
  font-family: "Roboto", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "";
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}
.App {
  max-width: 100%;
  width: 100%;
}

.FirstTask{max-width:500px;width:100%;margin:0 auto}.FirstTask-row{margin:0 auto;padding:10px;display:flex;width:100%;min-width:100%}.left{width:39%;margin-right:1%}.right{width:60%;min-width:60%}

.List{width:100%}.list-row{display:flex;flex-direction:column}.list-item{padding:10px;cursor:pointer}.list-item:hover{background-color:#9b9bc9;color:white}

.Details{width:100%}.img{max-width:100%;width:100%;height:290px;background-image:url(../../static/media/Preload.b2a8fa3d.gif);background-repeat:no-repeat;background-size:auto;background-position:center}.details-item{padding:10px;border:1px solid #d3c8c8;padding-left:15px}

